const connection = require('../date_base/conexion');

async function insertarRegistro(nombreOriginal, nombreConvertido, formatoDestino, idUsuario) {
  return new Promise((resolve, reject) => {
    connection.query('INSERT INTO historial_imagenes (nombre_original, nombre_convertido, formato, id_usuario) VALUES (?, ?, ?, ?)', [nombreOriginal, nombreConvertido, formatoDestino, idUsuario], (error, results) => {
      if (error) {
        console.error('Error al guardar en la base de datos:', error);
        reject(error);
      } else {
        resolve(results);
      }
    });
  });
}

module.exports = {
  insertarRegistro
};
